html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

body {
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: normal; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: local("Montserrat");
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
  src: url("../fonts/Montserrat-Regular.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: bold;
  font-weight: 700;
  src: local("Montserrat");
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2");
  src: url("../fonts/Montserrat-Bold.woff") format("woff"); }

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  src: local("Montserrat");
  src: url("../fonts/Poppins-Regular.woff2") format("woff2");
  src: url("../fonts/Poppins-Regular.woff") format("woff"); }

.header {
  min-height: 650px;
  background-image: url("../img/background-header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  padding-left: 45px;
  padding-right: 45px; }
  @media (max-width: 767px) {
    .header {
      padding-left: 15px;
      padding-right: 15px; } }

.header-wrapper {
  width: 1160px;
  margin-left: auto;
  margin-right: auto; }
  @media (max-width: 1250px) {
    .header-wrapper {
      width: 100%;
      min-width: 290px; } }

.header__navigation {
  padding-top: 35px;
  display: flex;
  justify-content: space-between; }

.header__navigation-logo-img {
  color: white; }

.header__navigation-toggle {
  display: none;
  background-color: transparent;
  border: none;
  width: 24px;
  height: 24px;
  justify-content: center;
  align-items: center;
  padding: 0; }
  .header__navigation-toggle:hover {
    cursor: pointer; }
  .header__navigation-toggle::before {
    content: '';
    width: 24px;
    height: 14px;
    background-image: url("../img/icon-menu-burger.svg"); }
  .header__navigation-toggle--open::before {
    content: '';
    width: 13px;
    height: 13px;
    background-image: url("../img/icon-menu-cross.svg");
    background-position: 50% 50%; }

.header__navigation-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  align-items: center; }

.header__navigation-item {
  margin-left: 33px; }

.header__navigation-link {
  color: white;
  font-size: 16px; }

.header__text-wrapper {
  margin-top: 210px; }

.header__text-title {
  font-weight: bold;
  font-size: 54px;
  line-height: 66px;
  color: white;
  margin-bottom: 25px; }

.header__text-subtitle {
  margin-top: 0;
  font-size: 20px;
  line-height: 24px;
  color: white; }

@media (max-width: 1023px) {
  .header {
    min-height: 638px; }
  .header__navigation {
    padding-top: 30px; }
  .header__navigation-logo-img {
    width: 38px;
    height: 20px; }
  .header__text-wrapper {
    margin-top: 230px; }
  .header__text-title {
    font-size: 50px;
    line-height: 61px; }
  .header__text-subtitle {
    font-size: 18px;
    line-height: 22px; } }

@media (max-width: 767px) {
  .header {
    min-height: 500px; }
  .header__navigation {
    padding-top: 20px; }
  .header__navigation-logo {
    z-index: 1; }
  .header__navigation-logo-img--open {
    color: #011C40; }
  .header__navigation-toggle {
    display: flex;
    z-index: 1; }
  .header__navigation {
    position: relative; }
  .header__navigation-list {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    background-color: white;
    padding-top: 92px; }
    .header__navigation-list--open {
      display: block; }
  .header__navigation-item {
    margin-left: 15px; }
  .header__navigation-link {
    color: #011C40;
    font-size: 18px;
    display: block;
    margin-bottom: 30px; }
  .header__text-wrapper {
    margin-top: 143px; }
  .header__text-title {
    font-size: 36px;
    line-height: 125%; }
  .header__text-subtitle {
    font-size: 16px;
    line-height: 140%; } }
